import { BriefConfig } from "@rp/react/tanstack-api/brief"
import { RPTooltip } from "@rp/react/web/common/base-components"
import { useBackOrNavigate, useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box, IconButton, Skeleton, Typography } from "@rp/react/web/mui/core"
import { ArrowBack } from "@rp/react/web/mui/icons"
import { BRIEF_ROUTE } from "@rp/react/web/router"
import { BriefConfigCardActionsDango } from "../BriefConfigCard/BriefConfigCardActionsDango"

type BriefDetailViewHeaderProps = {
  title: string | undefined
  briefConfig: BriefConfig | null
}

const BriefDetailViewHeader = ({ title, briefConfig }: BriefDetailViewHeaderProps) => {
  const { isMobile } = useDeviceInfo()
  const navigate = useBackOrNavigate()
  const onBackButtonClick = () => navigate(BRIEF_ROUTE)

  return (
    <Box
      sx={{
        position: "sticky",
        width: "100%",
        borderBottom: "1px solid",
        borderColor: "outline.border",
        top: 0,
        pr: 2,
        pl: 2,
        height: 56,
        backgroundColor: "surface.main",
        zIndex: 12,
        display: "flex",
        alignItems: "center",
        justifyContent: "space-between",
      }}
    >
      <Box
        sx={{
          display: "flex",
          alignItems: "center",
          gap: 1,
          minWidth: 0,
          justifyContent: "space-between",
          flex: 1,
        }}
      >
        <Box sx={{ display: "flex", alignItems: "center" }}>
          <RPTooltip title="Back" arrow={false}>
            <IconButton onClick={onBackButtonClick} size="small">
              <ArrowBack />
            </IconButton>
          </RPTooltip>

          {title ? (
            <Typography
              sx={{
                fontSize: isMobile ? 16 : 20,
                fontWeight: 700,
                whiteSpace: "nowrap",
                textOverflow: "ellipsis",
                overflow: "hidden",
                maxWidth: 550,
              }}
            >
              {title}
            </Typography>
          ) : (
            <Skeleton width={150} height={45} />
          )}
        </Box>

        <BriefConfigCardActionsDango briefConfig={briefConfig} />
      </Box>
    </Box>
  )
}
export { BriefDetailViewHeader }
